const parsedCategories = (category) => {
    let parsed_category;

    /**
     * category: category name
     * parsed_category: parsed category name
     * 
     * algorithm: 
     * - concept: 
     * - programmers: 
     * blog: 
     * jaylog: 
     * - interview: 
     * - retrospective: 
     * programming-language:  
     * books: 
     */

    switch (category) {
        case 'algorithm':
            parsed_category = '';
            break;
        case 'programmers':
            parsed_category = '';
            break;
        case 'concept':
            parsed_category = '';
            break;
        case 'blog':
            parsed_category = '';
            break;
        case 'jaylog':
            parsed_category = '';
            break;
        case 'interview':
            parsed_category = '';
            break;
        case 'retrospective':
            parsed_category = '';
            break;
        case 'programming-language':
            parsed_category = ' ';
            break;
        case 'books':
            parsed_category = ''; 
            break;
        case 'data-engineering':
            parsed_category = ' ';
            break;
        default:
            parsed_category = category;
            break;
    }
    return parsed_category;
};

export default parsedCategories;